		<resize-observer @notify="__resizeHanlder" />

		<el-row class="mt-0" :gutter="30">
			<el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="6">
				<div class="card-base card-shadow--medium mb-30 widget small-widget" v-loading="!asyncComponent">

					<div class="widget-header ph-20 pt-20">
						<div class="flex justify-center align-center">
							<div class="widget-icon-box mr-20 animated fadeInRight">
								<i class="widget-icon mdi mdi-bus accent-text fs-30"></i>
							</div>
							<div class="widget-info box grow text-truncate animated fadeInLeft">
								<div class="o-050 widget-title text-truncate pt-5 pb-10">Stops</div>
								<h2 class="m-0 text-truncate">{{ counts.stops }}</h2>
							</div>
						</div>
					</div>

					<component :is="asyncComponent" :type="'line'" :options='{ width: "100%", height: 80 }' :data="[1, 3, 2, 4, 4, 9, 3, 4, 6, 5, 4, 6, 9, 8, 11, 12, 13, 12, 12, 14].toString()"/>
				</div>
			</el-col>
			<el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="6">
				<div class="card-base card-shadow--medium mb-30 widget small-widget" v-loading="!asyncComponent">

					<div class="widget-header ph-20 pt-20">
						<div class="flex justify-center align-center">
							<div class="widget-icon-box mr-20 animated fadeInRight">
								<i class="widget-icon mdi mdi-routes fs-30"></i>
							</div>
							<div class="widget-info box grow text-truncate animated fadeInLeft">
								<div class="o-050 widget-title text-truncate pt-5 pb-10">Routes</div>
								<h2 class="m-0 text-truncate">{{ counts.routes }}</h2>
							</div>
						</div>
					</div>

					<component :is="asyncComponent" :type="'bar'" :options='{ width: "100%", height: 80, fill: ["#c6d9fd"] }' :data="[1, 3, 2, 4, 4, 9, 3, 4, 6, 5, 4, 6, 9, 8, 7, 6, 5, 12, 10, 9].toString()"/>
				</div>
			</el-col>
			<el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="6">
				<div class="card-base card-shadow--medium mb-30 widget small-widget" v-loading="!asyncComponent">

					<div class="widget-header ph-20 pt-20">
						<div class="flex justify-center align-center">
							<div class="widget-icon-box mr-20 animated fadeInRight">
								<i class="widget-icon mdi mdi-cash success-text fs-30"></i>
							</div>
							<div class="widget-info box grow text-truncate animated fadeInLeft">
								<div class="o-050 widget-title text-truncate pt-5 pb-10">Fares</div>
								<h2 class="m-0 text-truncate">{{ counts.fares }}</h2>
							</div>
						</div>
					</div>

					<component :is="asyncComponent" :type="'line'" :options='{ width: "100%", height: 80, fill: "#56f19a", stroke: "#67C23A" }' :data="[10, 7, 8, 5, 4, 9, 3, 4, 6, 5, 4, 4, 2, 4, 5, 9, 13, 12, 12, 14].toString()"/>
				</div>
			</el-col>
			<el-col :xs="24" :sm="12" :md="12" :lg="6" :xl="6">
				<div class="card-base card-shadow--medium mb-30 widget small-widget" v-loading="!asyncComponent">

					<div class="widget-header ph-20 pt-20">
						<div class="flex justify-center align-center">
							<div class="widget-icon-box mr-20 animated fadeInRight">
								<i class="widget-icon mdi mdi-comment-question success-text fs-30"></i>
							</div>
							<div class="widget-info box grow text-truncate animated fadeInLeft">
								<div class="o-050 widget-title text-truncate pt-5 pb-10">Responses</div>
								<h2 class="m-0 text-truncate">{{ counts.responses }}</h2>
							</div>
						</div>
					</div>

					<component :is="asyncComponent" :type="'bar'" :options='{ width: "100%", height: 80, fill: ["#56f19a"] }' :data="[6, 5, 4, 3, 5, 3, 4, 5, 6, 5, 4, 2, 3, 8, 7, 6, 5, 2, 1, 5].toString()"/>
				</div>
			</el-col>
		</el-row>

		<el-row class="mt-30" :gutter="30">
			<el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
				<div class="card-base card-shadow--medium mb-30 bg-primary p-20" style="height:410px; box-sizing:border-box;" v-loading="!asyncChart">
					<h1 class="white-text mv-0 animated fadeInDown">Data Statistics</h1>
					<div id="chart" style="height:350px; width:80%; margin-left:10%"></div>
				</div>
			</el-col>
		</el-row>
	</vue-scroll>
</template>

<script>
import echarts from 'echarts';
import { Timeline, TimelineItem, TimelineTitle } from 'vue-cute-timeline';

export default {
	name: 'Dashboard',
	data () {
		return {
			asyncComponent: 'peity',
			asyncChart: true,
			chart1: null,
			resized: false,
			counts: {
				stops: 22221,
				routes: 818,
				fares: 284,
				responses: 1398
			},
		}
	},
	methods: {
		__resizeHanlder: _.throttle(function (e) {
			if(this.resized) {
				this.asyncComponent = null;
				this.removePeity();
				setTimeout(()=>{this.asyncComponent = 'peity'}, 1000);

				this.asyncChart = false;
				setTimeout(()=>{this.asyncChart = true; this.resizeChart()}, 2500);
			}
			this.resized = true
		}, 700),
		removePeity() {
			const peityEl = document.querySelectorAll('.widget .peity'); //.forEach((el)=>{el.remove()})
			//ie fix
			for(let i=0; i<peityEl.length; i++) {peityEl[i].parentNode.removeChild(peityEl[i])}
		},
		initChart() {
			this.chart = echarts.init(document.getElementById('chart'));

			const data2018 = [
				[818,22221,284,11,1398]
			];

			const data2017 = [
				[686,19421,125,0,0]
			];

			const lineStyle = {
				normal: {
					width: 1,
					opacity: 0.5
				}
			};

			this.chart.setOption({
				legend: {
					bottom: 0,
					data: ['This year', 'Last year'],
					itemGap: 20,
					textStyle: {
						color: '#fff',
						fontSize: 14
					},
					selectedMode: 'single'
				},
                tooltip: {},
				grid: {
					height: '350px'
				},
				radar: {
					indicator: [
						{name: 'ROUTES', max: 1000},
						{name: 'STOPS', max: 25000},
						{name: 'FARES', max: 300},
						{name: 'QUESTIONS', max: 20},
						{name: 'RESPONSES', max: 1500}
					],
					shape: 'circle',
					radius : '60%',
					splitNumber: 5,
					name: {
						textStyle: {
							color: 'rgb(238, 197, 102)'
						}
					},
					splitLine: {
						lineStyle: {
							color: [
								'rgba(238, 197, 102, 0.1)', 'rgba(238, 197, 102, 0.2)',
								'rgba(238, 197, 102, 0.4)', 'rgba(238, 197, 102, 0.6)',
								'rgba(238, 197, 102, 0.8)', 'rgba(238, 197, 102, 1)'
							].reverse()
						}
					},
					splitArea: {
						show: false
					},
					axisLine: {
						lineStyle: {
							color: 'rgba(238, 197, 102, 0.5)'
						}
					}
				},
				series: [
					{
						name: 'This year',
						type: 'radar',
						lineStyle: lineStyle,
						data: data2018,
						symbol: 'none',
						itemStyle: {
							normal: {
								color: '#F9713C'
							}
						},
						areaStyle: {
							normal: {
								opacity: 0.1
							}
						}
					},
					{
						name: 'Last year',
						type: 'radar',
						lineStyle: lineStyle,
						data: data2017,
						symbol: 'none',
						itemStyle: {
							normal: {
								color: '#B3E4A1'
							}
						},
						areaStyle: {
							normal: {
								opacity: 0.05
							}
						}
					}
				]
			})
		},
		destroyChart() {
			if(this.chart) {
				this.chart.dispose()
				this.chart = null
			}
		},
		resizeChart() {
			if (this.chart) {
				this.chart.resize()
			}
		},
	},
	mounted() {
		this.initChart()
	},
	beforeDestroy() {
		this.destroyChart()
	},
	components: {
		Timeline,
		TimelineItem,
		TimelineTitle
	}
}
</script>

