                return t[r + 1] ? void 0 : "stroke-width: 0"
            }).attr("y1", function (t, e) {
                return s(e * u + l)
            }).attr("x1", function (t) {
                return o((n - t.frequencyTotal) / 2) + 100
            }).attr("y2", function (t, e) {
                return s((e + 1) * u)
            }).attr("x2", function (e, r) {
                return t[r + 1] ? o((n - t[r + 1].frequencyTotal) / 2) + 100 : 0
            });
            var k = d.selectAll("line.right_line").data(t);
            k.enter().append("line").attr("class", "right_line").attr("y2", function (t, e) {
                return s(e * u + l)
            }).attr("x2", function (t) {
                return o((n - t.frequencyTotal) / 2 + t.frequencyTotal) + 100
            }), k.transition().duration(1e3).attr("style", function (e, r) {
                return t[r + 1] ? void 0 : "stroke-width: 0"
            }).attr("y1", function (t, e) {
                return s(e * u + l)
            }).attr("x1", function (t) {
                return o((n - t.frequencyTotal) / 2 + t.frequencyTotal) + 100
            }).attr("y2", function (t, e) {
                return s((e + 1) * u)
            }).attr("x2", function (e, r) {
                return t[r + 1] ? o((n - t[r + 1].frequencyTotal) / 2 + t[r + 1].frequencyTotal) + 100 : 0
            })
        },
        filter(t) {
            this.options.filterList.length < 1 && (this.options.filterList = jQuery.extend(!0, [], this.options.fullList));
            for (let e in t) {
                let r = t[e].choices,
                    n = [];
                for (let i in r) jQuery.inArray(r[i].choice_text, this.options.filterList) >= 0 && n.push(r[i]);
                t[e].choices = n
            }
            return t
        },
        onlyFilter(t) {
