<template>
    <div class="fare-per-route">
        <h1 class="mv-0 animated fadeInDown">MATATU STOPS IN AND NEIGHBOURING NAIROBI</h1>
        <el-row>
            <div class="route-buttons-map">
                <svg>
                    <g>
                    <path :d="line" style="transform: translate(0, -40px)"/>
                    </g>
                </svg>
            </div>
        </el-row>
    </div>
</template>

<script>
import * as d3 from "d3";
import * as topojson from 'topojson';

import stopsList from '../data/stops.json';
import nairobi from '../data/nairobi.json';
import routesList from '../data/routes.json';

export default {
	name: 'Stops',
	data() {
		return {
            line: '',
            dataJson: nairobi,
            originGeo: [36.82488441, -1.285684705],
            stops: stopsList.stops,
            svg: null,
            projection: null,
            path: null,
            tooltip: null,
            speed: 2800, //km/sec
        }
	},
	mounted() {
	    this.init();
	},
	methods: {
	    init() {
            let featureCollection = topojson.feature(this.dataJson, this.dataJson.objects.boundaries);
            let bounds = d3.geoBounds(featureCollection);

            let centerX = d3.sum(bounds, function(d) {return d[0];}) / 2,
                centerY = d3.sum(bounds, function(d) {return d[1];}) / 2;

            this.projection = d3.geoMercator()
                .scale(75000)
                .center([centerX, centerY]);

            this.path = d3.geoPath().projection(this.projection);
            this.line = this.path(featureCollection);

            this.tooltip = d3.select('.route-buttons-map').append('div')
                .attr('class', 'tooltipDestination')
                .style('position', 'absolute')
                .style('background', 'black')
                .style('color', 'white')
                .style('opacity', 0);

            this.svg = d3.select('svg')
                .call(d3.zoom().on("zoom", () => {
                    this.svg.attr("transform", d3.event.transform)
                }));

            this.initStops();
            this.drawRoutes();
        },
        initStops() {
	        let source = this.svg.selectAll('circleOrigin');
            let self = this;
            source
                .data([this.originGeo]).enter()
                .append("image")
                .attr("xlink:href", "/static/icons/android-icon-36x36.png")
                .attr("x", (d) => { return this.projection(d)[0]; })
                .attr("y", (d) => { return this.projection(d)[1]; })
                .attr("width", 16)
                .attr("height", 16)
                .on('mouseover', (d) => {
                    self.tooltip.html('<span>Kencom (CBD)</span>')
                        .attr('class', 'tooltipOrigin')
                        .style('left', this.projection(d)[0] + 12 + 'px')
                        .style('top', this.projection(d)[1] - 20 + 'px')
                        .transition()
                        .duration(700)
                        .style('opacity', 1)
                })
                .on('mouseout', () => {
                    self.tooltip.transition()
                        .duration(700)
                        .style('opacity', 0)
                });

	        this.drawConnection(0);
        },
        drawConnection(index) {
	        let self = this;
            let destination = self.stops[index];
            let originPos = this.projection(self.originGeo);
            let destinationPos = this.projection(destination.position);
            let connection = [ originPos, destinationPos ];
            let destinationName = destination.name;
            let originGeo = this.originGeo;
            let destinationGeo = destination.position;
            let svg = this.svg;
            let distance = self.calculateDistance(originGeo[1], originGeo[0], destinationGeo[1], destinationGeo[0]);
            let duration = self.calculateDuration(distance);
            svg.append('path')
                .datum(connection)
                .attr('class', 'arc' + index)
                .attr('d', function(coordinates) {
                    let d = {
                        origin: { x: coordinates[0][0], y: coordinates[0][1]},
                        destination: { x: coordinates[1][0], y: coordinates[1][1]}
                    };
                    let s = false;
                    if (d.destination.x > d.origin.x) {
                        s = true;
                    }
                    return self.getArc(d, s);
                })
                .style('stroke', 'steelblue')
                .style('stroke-width', 2)
                .style('fill', 'none')
                .transition()
                .duration(duration)
                .attrTween('stroke-dasharray', function() {
                    let len = this.getTotalLength();
                    return function(t) {
                        return (d3.interpolate('0,' + len, len + ',0'))(t)
                    };
                })
                .on('end', function(d) {
                    let c = connection[1];
                    svg.append("image")
                        .datum(c)
                        .attr("xlink:href", "/static/icons/android-icon-36x36.png")
                        .attr("x", c[0])
                        .attr("y", c[1])
                        .attr("width", 16)
                        .attr("height", 16)
                        .on('mouseover', function (d) {
                            self.tooltip.html('<span>' + destinationName + '</span>')
                                .attr('class', 'tooltipDestination')
                                .style('left', d[0] + 12 + 'px')
                                .style('top', d[1] - 20 + 'px')
                                .transition()
                                .duration(700)
                                .style('opacity', 1)
                        })
                        .on('mouseout', function (d) {
                            self.tooltip.transition()
                                .duration(700)
                                .style('opacity', 0)
                        })
                        .transition()
                        .duration(0)
                        .attr('r', '3px')
                        .on('end', function() {
                            d3.select(this)
                                .transition()
                                .duration(2000)
                                .attr('r', 20)
                                .style('fill-opacity', '0');
                            d3.select('.arc' + index)
                                .transition()
                                .duration(2000)
                                .style('stroke-opacity', '0')
                                .style('stroke-width', '1')
                                .on('end', function (d) {
                                    if (index === self.stops.length - 1) {
                                        svg.selectAll('.destCircleInner').remove();
                                        svg.selectAll('.destCircleOuter').remove();
                                        svg.selectAll('.destCircleMouse').remove();
                                        for (let i = 0; i < self.stops.length; i++) {
                                            svg.selectAll('.arc' + i).remove();
                                        }
                                    }
                                    let nextIndex = index + 1;
                                    if (nextIndex < self.stops.length) {
                                        self.drawConnection(nextIndex);
                                    } else {
                                        self.drawConnection(0);
                                    }
                                })
                        });
                });
        },
        getArc(d, s) {
            let dx = d.destination.x - d.origin.x,
                dy = d.destination.y - d.origin.y,
                dr = Math.sqrt(dx * dx + dy * dy);
            let spath = s === false ? ' 0 0,0 ' : ' 0 0,1 ';
            return 'M' + d.origin.x + ',' + d.origin.y + 'A' + dr + ',' + dr + spath + d.destination.x + ',' + d.destination.y;
        },
        calculateDistance(lat1, lon1, lat2, lon2) {
            let p = 0.017453292519943295,
                c = Math.cos;
            let a = 0.5 - c((lat2 - lat1) * p)/2 + c(lat1 * p) * c(lat2 * p) * (1 - c((lon2 - lon1) * p))/2;
            return 12742 * Math.asin(Math.sqrt(a));
        },
        calculateDuration(distance) {
            return (distance / this.speed) * 1000;
        },
        drawRoutes() {
	        let lines = this.svg.append("g").attr("class", "map_lines");
	        lines.selectAll("path.map_line")
                .data(topojson.feature(routesList, routesList.objects.KEN_Roads).features)
                .enter()
                .append("path")
                .attr("d", this.path)
                .attr("class", "map_line l_")
                .style("stroke", "yellow")
                .style("fill", "white")
                .style("fill-opacity", 0)
                .style("opacity", 1.5)
                .style("stroke-width", 1.5)
                .style("stroke-linecap", "round");
        }
	}
}
</script>

