import Vue from 'vue';
import Router from 'vue-router';
import jwt_decode from 'jwt-decode'

//apps
import Dashboard from '../components/Dashboard.vue';
import Stops from '../components/charts/Stops.vue';
import FilteredQuestions from '../components/charts/Questions.vue';
// import FilteredQuestions from '../components/charts/FilteredQuestions.vue';
import WeightedQuestions from '../components/charts/WeightedQuestions.vue';

//user account mgt
import Login from '../components/authentication/Login.vue';
import Profile from '../components/profile/Profile.vue';

import NotFound from '../components/NotFound.vue';

import layouts from '../layout';
import store from '../store';

Vue.use(Router);


const router = new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			alias: '/dashboard',
			name: 'dashboard',
			component: Dashboard,
			meta: {
				layout: layouts.navLeft,
				searchable: true,
				tags: ['app']
			}
		},
		{
			path: '/stops',
			name: 'stops',
			component: Stops,
			meta: {
				layout: layouts.navLeft,
				searchable: true,
				tags: ['app']
			}
		},
		{
			path: '/questions',
			name: 'questions',
			component: FilteredQuestions,
			meta: {
				layout: layouts.navLeft,
				searchable: true,
				tags: ['pages']
			}
		},
		{
			path: '/questions/filter',
			name: 'filteredQuestions',
			component: WeightedQuestions,
			meta: {
				layout: layouts.navLeft,
				searchable: true,
				tags: ['pages']
			}
		},
		{
			path: '/profile',
			name: 'profile',
			component: Profile,
			meta: {
				auth: true,
				layout: layouts.navLeft,
				searchable: true,
				tags: ['pages']
			}
		},
		{
			path: '/login',
			name: 'login',
			component: Login,
			meta: {
				layout: layouts.contenOnly
			}
		},
		{ 
			path: '/logout',
			beforeEnter (to, from, next) {
				auth.logout();
				next({path:'/login'})
			}
		},
		{
			path: '*',
			name: 'not-found',
			component: NotFound,
			meta: {
				layout: layouts.contenOnly
			}
		}
	]
})


const l = {
	contenOnly(){
		store.commit('setLayout', layouts.contenOnly)
	},
	navLeft(){
		store.commit('setLayout', layouts.navLeft)
	},
	navRight(){
		store.commit('setLayout', layouts.navRight)
	},
	navTop(){
		store.commit('setLayout', layouts.navTop)
	},
	navBottom(){
		store.commit('setLayout', layouts.navBottom)
	},
	set(layout){
		store.commit('setLayout', layout)
	}
};

//insert here login logic
const auth = {
	loggedIn() {
        const token = localStorage.getItem("token");
        if (token && token !== "null") {
            const decoded = jwt_decode(token);
            const exp = decoded.exp;
            if(exp - Date.now() / 1000 >= 1800){
                return true;
            } else {
                store.commit("setLogout");
                return false;
            }
        } else {
            store.commit("setLogout");
            return false;
        }
	},
	logout() {
		store.commit('setLogout')
	}
};

router.beforeEach((to, from, next) => {
	let authRequired = !!(to && to.meta && to.meta.auth);

	if(authRequired) {
		if(auth.loggedIn()) {
			if(to.name === 'login') {
				window.location.href = '/';
				return false
			} else {
				next()
			}
		} else {
			if(to.name !== 'login'){
				window.location.href = '/login';
				return false
			}
			next()
		}
	} else {
		if(auth.loggedIn() && to.name === 'login'){
			window.location.href = '/';
			return false
		} else {
			next()
		}
	}

	if(to && to.meta && to.meta.layout){
		l.set(to.meta.layout)
	}
});


router.afterEach(() => {
	setTimeout(()=>{
		store.commit('setSplashScreen', false)
	}, 500)
});

export default router