<template>
	<div class="page-profile-edit">
		<el-form :model="basic" label-width="120px" label-position="top">
			<el-col>
				<el-col :span="12" :md="12" :sm="24" :xs="24" class="col-p">
					<el-form-item label="Username">
						<el-input v-model="basic.username"/>
					</el-form-item>
				</el-col>
				<el-col :span="12" :md="12" :sm="24" :xs="24" class="col-p">
					<el-form-item label="Email">
						<el-input v-model="basic.email" type="email"/>
					</el-form-item>
				</el-col>
			</el-col>
			<el-col class="col-p">
				<el-form-item>
					<el-button type="primary" @click="updateBasic">Save</el-button>
					<el-button>Cancel</el-button>
				</el-form-item>
			</el-col>
		</el-form>
		<el-form :model="password" label-width="120px" label-position="top">
			<el-col>
				<el-col :span="24" :md="24" :sm="24" :xs="24" class="col-p">
					<el-form-item label="Existing Password">
						<el-input v-model="password.old"/>
					</el-form-item>
				</el-col>
				<el-col :span="12" :md="12" :sm="24" :xs="24" class="col-p">
					<el-form-item label="New Password">
						<el-input v-model="password.new"/>
					</el-form-item>
				</el-col>
				<el-col :span="12" :md="12" :sm="24" :xs="24" class="col-p">
					<el-form-item label="Confirm password">
						<el-input v-model="password.confirmation" type="email"/>
					</el-form-item>
				</el-col>
			</el-col>
			<el-col class="col-p">
				<el-form-item>
					<el-button type="primary" @click="updatePassword">Save</el-button>
					<el-button>Cancel</el-button>
				</el-form-item>
			</el-col>
		</el-form>
	</div>
</template>

<script>
import axios from 'axios';
export default {
	name: 'ProfileEdit',
	data() {
		return {
		    basicDetailsURL: '/account/settings/',
			passwordURL: '/account/password/',
			basic: {
				username: '',
				email: ''
			},
			password: {
			  	old: '',
				new: '',
				confirmation: ''
			}
		}
	},
	methods: {
		resizeLabelPosition() {
			if(window.innerWidth <= 768) {
				this.labelPosition = 'top'	
			}
		},
        init() {
            axios.get(this.basicDetailsURL)
                .then((response) => {
                    if(response.data.success) {
                        this.basic.username = response.data.username;
                        this.basic.email = response.data.email;
                    }
                    else {
                        alert('an error occurred');
					}
                }).catch(() => {
                alert('an error occurred');
            });
        },
		updateBasic() {
		    axios.post(this.basicDetailsURL, this.basic)
				.then((response) => {
					if(!response.data.success) {
					    alert('an error occurred');
					}
				}).catch(() => {
                	alert('an error occurred');
				});
		},
		updatePassword() {
		    let self = this;
		    axios.post(self.passwordURL, self.password)
				.then(function(){

				});
		}
	},
	mounted() {
		this.resizeLabelPosition();
		window.addEventListener('resize', this.resizeLabelPosition);
        this.init();
    },
	beforeDestroy() {
		window.removeEventListener('resize', this.resizeLabelPosition);
	}
}
</script>

