import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';
import createPersistedState from 'vuex-persistedstate'
import jwt_decode from "jwt-decode"

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
	state: {
		layout: {
			navPos: 'left',
			toolbar: 'top',
			footer: true,
			boxed: false,
			roundedCorners: false,
			viewAnimation: 'fade-top'
		},
		splashScreen: true,
		logged: false,
		jwt: localStorage.getItem('token'),
		endpoints: {
            base: "http://41.89.64.3/ma3tycoon/",
		  	obtainJWT: 'auth/jwt/create/',
		  	refreshJWT: 'auth/beam/m33/up/'
		}
	},
	mutations: {
		setLayout(state, payload) {
			if(payload && payload.navPos)
				state.layout.navPos = payload.navPos

			if(payload && payload.toolbar)
				state.layout.toolbar = payload.toolbar
				
			if(payload && payload.footer)
				state.layout.footer = payload.footer
		
			if(payload && payload.boxed)
				state.layout.boxed = payload.boxed

			if(payload && payload.roundedCorners)
				state.layout.roundedCorners = payload.roundedCorners

			if(payload && payload.viewAnimation)
				state.layout.viewAnimation = payload.viewAnimation
		},
		setLogin(state, payload) {
			state.logged = true;
			state.token = payload;
		},
		setLogout(state, payload) {
			// state.layout.navPos = null;
			// state.layout.toolbar = null;
			state.logged = false;
		},
		setSplashScreen(state, payload) {
			state.splashScreen = payload
		},
		updateToken(state, newToken){
		  	localStorage.setItem('token', newToken);
		  	state.jwt = newToken;
		},
		removeToken(state){
		  	localStorage.removeItem('token');
		  	state.jwt = null;
		}
	},
	actions: {
		obtainToken(username, password){
		  	const payload = {
				username: username,
				password: password
		  	}
		  	console.log("get");
		  	axios.post(this.state.endpoints.obtainJWT, payload)
				.then((response)=>{
					this.commit('updateToken', response.data.token);
			  	})
				.catch((error)=>{
					console.log(error);
			  	})
		},
		refreshToken(){
		  	axios.post(this.state.endpoints.refreshJWT, {token: this.state.jwt})
				.then((response)=>{
					this.commit('updateToken', response.data.token)
			  	})
				.catch((error)=>{
					console.log(error)
			  	})
		},
		inspectToken(){
		  	const token = this.state.jwt;
		  	if(token && token !== "null"){
				const decoded = jwt_decode(token);
				const exp = decoded.exp;
				if(exp - Date.now() / 1000 >= 1800){
			  		this.dispatch('refreshToken')
				} else {
                    this.commit("setLogout");
				}
		  	}
            else {
                this.commit("setLogout");
            }
		}
	},
	getters: {
		layout(state, getters) {
			return state.layout
		},
		navPos(state, getters) {
			return state.layout.navPos
		},
		toolbar(state, getters) {
			return state.layout.toolbar
		},
		footer(state, getters) {
			return state.layout.footer
		},
		boxed(state, getters) {
			return state.layout.boxed
		},
		roundedCorners(state, getters) {
			return state.layout.roundedCorners
		},
		viewAnimation(state, getters) {
			return state.layout.viewAnimation
		},
		isLogged(state, getters) {
			return state.logged
		},
		splashScreen(state, getters) {
			return state.splashScreen
		},
		token(state, getters) {
			return state.token
		},
        baseURL: state => {
            return state.endpoints.base;
        },
        endpoints: state => {
            return state.endpoints;
        },
	},
	plugins: [createPersistedState({paths: ['layout']})],
	strict: debug
})
