import 'es6-promise/auto'
import _ from 'lodash'

// npm packages
import Vue from 'vue'
import axios from 'axios'
import VueI18n from 'vue-i18n'
import Element from 'element-ui'
import locale from 'element-ui/lib/locale/lang/en'
import Vuebar from 'vuebar'
import Viewer from 'v-viewer'
import Fullscreen from 'vue-fullscreen'
import VueLazyload from 'vue-lazyload'
import VueHighlightJS from 'vue-highlightjs'
import vClickOutside from 'v-click-outside'
import VueResize from 'vue-resize'
import VueFloatLabel from 'vue-float-label'
import { StatusIndicator } from 'vue-status-indicator'
import Bars from 'vuebars'
import Trend from 'vuetrend'
import VueChartist from 'v-chartist'
import D3Network from 'vue-d3-network'
import Chart from 'chart.js'
import BulmaChart from 'vue-bulma-chartjs'
import Peity from 'vue-peity'
import VueChartkick from 'vue-chartkick'
import VuePerfectScrollbar from './components/vue-ps'

// local packages
import Affix from './directives/affix'
import App from './App.vue'
import router from './router/'
import store from './store'
import i18n_messages from './i18n.json'

// css
import './assets/scss/global.scss'
import 'flex.box'
import 'animate.css'
import 'flag-icon-css/css/flag-icon.css'
import '../node_modules/mdi/scss/materialdesignicons.scss'
import 'viewerjs/dist/viewer.css'
import 'highlight.js/styles/color-brewer.css'
import 'vue-resize/dist/vue-resize.css'
import 'vue-status-indicator/styles.css'
import 'chartist/dist/chartist.css'
import 'vue-d3-network/dist/vue-d3-network.css'

Vue.component('StatusIndicator', StatusIndicator);
Vue.component('Peity', Peity);
Vue.component('BulmaChart', BulmaChart);
Vue.component('D3Network', D3Network);
Vue.component('VueChartist', VueChartist);
Vue.component('vue-scroll', VuePerfectScrollbar);

Vue.use(axios);
Vue.use(VueChartkick, {adapter: Chart});
Vue.use(Trend);
Vue.use(Bars);
Vue.use(VueFloatLabel);
Vue.use(VueResize);
Vue.use(Fullscreen);
Vue.use(VueI18n);
Vue.use(Element, {locale});
Vue.use(VueHighlightJS);
Vue.use(vClickOutside);
Vue.use(Viewer);
Vue.use(Vuebar);
Vue.use(VueLazyload, {
	preLoad: 1.3,
	error: 'data:image/svg+xml;base64,',
	loading: 'data:image/svg+xml;base64,',
	attempt: 1
});

// directives
Vue.directive('affix', Affix);
require("./directives/errorHandler.js");

// globalization
const i18n = new VueI18n({ locale: 'us', messages:i18n_messages });

// config
Vue.config.productionTip = false;
window.$ = window.jQuery = require("jquery");
window.axios = axios;
window.axios.defaults.baseURL =
    process.env.NODE_ENV !== "production" ? store.getters.baseURL : "";

new Vue({
	i18n,
	router,
	store,
    beforeMount: function() {
        store.dispatch("inspectToken");
        if (store.getters.token && store.getters.token !== "null") {
            axios.defaults.headers.common["Authorization"] =
                "JWT " + store.getters.token;
        }
    },
	render: h => h(App)
}).$mount('#app');
