<template>
	<vue-scroll class="page-profile" id="affix-container">
		<div class="card-base card-shadow--medium info bg-white black-text">
			<profile-edit></profile-edit>
		</div>
	</vue-scroll>
</template>

<script>
import Affix from '@/components/Affix'
import ProfileEdit from '@/components/profile/ProfileEdit'

export default {
	name: 'Profile',
	data() {
		return {
			username: '',
			colorActive: false,
			color: 'white',
			affixEnabled: true
		}
	},
	methods: {
		resizeAffixEnabled() {
			this.affixEnabled = (window.innerWidth > 768);
		},
		getUsernameFromJWT() {
		    let base64Url = localStorage.getItem('token').split('.')[1];
            let base64 = base64Url.replace('-', '+').replace('_', '/');
            let user_dict = JSON.parse(window.atob(base64));
            return user_dict.username || '';
		}
	},
	mounted() {
	    this.username = this.getUsernameFromJWT();

		this.resizeAffixEnabled();
		window.addEventListener('resize', this.resizeAffixEnabled);
	},
	beforeDestroy() {
		window.removeEventListener('resize', this.resizeAffixEnabled);
	},
	components: {
		Affix,
		ProfileEdit
	}
}
</script>

